/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from "react";
import SpecificCardDisplay from "./SpecificCardDisplay";
import Nav from "../Nav/Nav"
import './Cards.css';

const Quiz = () =>{

    const [cardInfo, setCardInfo] = useState([
        {id: 101, title: 'Linux-Commands', desc:'10 QUESTIONS', complete: false },
        {id: 102, title: 'Secure-Coding', desc:'10 QUESTIONS', complete: false},
        {id: 103, title: 'Risk-Management', desc:'5 QUESTIONS', complete: false },
        
    
    ])

    useEffect(()=>{
        // call to database for all contest info
        // fetch(data);
        // setCardInfo(data);

    },[])
    return(
        <div className="Cards bg">
            <Nav/>
            <h1 className="heading">Quiz</h1>
            <div className="cardWrapper">
                {cardInfo.map((card)=>{
                    return(
                        <SpecificCardDisplay className="card" state={card} key={card.id} />
                    )
                })}
            </div>
        </div>
    );
}

export default Quiz;